<template>
  <div class="register">
    <div class="container">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-7 register__section">
          <h1>{{heading_text}}</h1>
          <h3>{{heading_sub_text}}</h3>
          <div class="success" v-if="showSuccess">
              <p class="text-success">{{success_message}}</p>
          </div>
          <div class="register__box" v-if="show_register">
            
            <b-form @submit="onSubmit" @reset="onReset">
            <div class="error" v-if="showErrors">
              <ul v-if="showErrors">
                <li v-for="(item, i) in errs" :key="i">{{item}}</li>
              </ul>
            </div>
            <b-form-group
              id="tenTongDai"
              label="Tn tng i:"
              label-for="name_tongdai"
            >
              <b-form-input
                id="name_tongdai"
                type="text"
                v-model="form.name_tongdai"
                required
                placeholder="Tn tng i"
              ></b-form-input>
            </b-form-group>

            <b-form-group
              id="tenCongTy"
              label="Tn cng ty:"
              label-for="name_congty"
            >
              <b-form-input
                id="name_congty"
                type="text"
                v-model="form.name_congty"
                required
                placeholder="Tn cng ty"
              ></b-form-input>
            </b-form-group>
            <b-form-group id="fullName" label="Tn ca bn:" label-for="fullName">
              <b-form-input
                id="fullName"
                type="text"
                v-model="form.name"
                required
                placeholder="Tn ca bn"
              ></b-form-input>
            </b-form-group>
            <b-form-group
              id="email"
              label="a ch Email:"
              label-for="email"
            >
              <b-form-input
                id="emailAddress"
                type="email"
                v-model="form.email"
                required
                placeholder="a ch mail"
              ></b-form-input>
            </b-form-group>
            <b-form-group
              id="sdt"
              label="S in thoi:"
              label-for="phone"
            >
              <b-form-input
                id="phone"
                type="text"
                v-model="form.phone"
                required
                placeholder="S in thoi"
              ></b-form-input>
            </b-form-group>
           
            <b-button type="submit" class="btn_register">ng k</b-button>
          </b-form>
          </div>

          <div class="otp__box" v-if="show_otp">
            <b-form @submit="onOtpSubmit" @reset="onOtpReset">    
              <p>Chng ti va gi m xc nhn (OTP) ti s thu bao ca bn, vui lng nhp di y.</p>
              <p><span>Cha nhn c? <a href="" @click.prevent="resendOTP()">Gi li</a></span></p>
              <p class="otp_retry text-center" v-if="show_text_retry">H thng  gi li bn m OTP mi !</p>
              <b-form-input
                  id="otp_code"
                  type="text"
                  v-model="form.otp_code"
                  required
                  placeholder="Nhp m OTP"
              ></b-form-input>
              <span class="otp_errors" v-if="showOtpError">{{otp_error}}</span>
              <div class="btn_submit_otp_wrap">
                <b-button type="submit" class="btn_submit_otp">Xc nhn</b-button>
              </div>
            </b-form>
          </div>
        </div>
        <div class="col-xs-12 col-sm-12 col-md-5">
          <img src="../../assets/register/robo.png" alt="" class="img-fluid"/>
        </div>
        
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: "Register",
  data() {
    return {
      heading_text: "ng k AI Call Center",
      heading_sub_text: "Tri nghim tng i t ng",
      success_message: "Cm n bn  ng k s dng AI Call Center, B phn chm sc khch hng ca chng ti s lin h vi qu khch trong thi gian sm nht.",
      show_register: true,
      show_otp: false,

      form: {
        email: "",
        name: "",
        phone: "",
        name_congty: "",
        name_tongdai: "",
        user_id: 0,
        otp_code: ""
      },
      show: true,
      showErrors: false,
      showSuccess: false,
      errs: [],
      otp_error: '',
      showOtpError: false,
      show_text_retry: false,
      };
  },
  methods: {
    onSubmit(evt) {
      let me = this;
      evt.preventDefault();
      axios.post('ajaxfile.php', {
        request: 2,
        tongdai: this.form.name_tongdai,
        congty: this.form.name_congty,
        name: this.form.name,
        email: this.form.email,
        phone: this.form.phone
      })
      .then(function (response) {
        if(response.data.hasOwnProperty('err')) {
          me.showErrors = true;
          me.errs = [];
          me.errs.push(...response.data.err);
        } else {
          me.showErrors = false;
          me.show_register = false;
          me.heading_text = "Ch cn mt bc na!";
          me.heading_sub_text = "Bo mt ca bn l u tin ca chng ti";
          me.show_otp = true;
          
          //me.showSuccess = true;
          //let formGroups = me.$el.querySelectorAll('.form-group');
          // for (let i of formGroups) {
          //   i.style.display = "none";
          // }
          // me.$el.querySelector('.btn_register').style.display = "none";  
        }
      })
    },
    onReset(evt) {
      evt.preventDefault();
      /* Reset our form values */
      this.form.email = "";
      this.form.name = "";
      this.form.name_congty = "";
      this.form.name_tongdai = "";
      this.form.phone = "";
      this.form.checked = [];
      /* Trick to reset/clear native browser form validation state */
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    },
    onOtpSubmit(evt) {
      let me = this;
      evt.preventDefault();
      axios.post('ajaxfile.php', {
        request: 3,
        otp_code: parseInt(this.form.otp_code),
        tongdai: this.form.name_tongdai,
        congty: this.form.name_congty,
        name: this.form.name,
        email: this.form.email,
        phone: this.form.phone
      })
      .then(function (response) {
        if(response.data === 0) {
          me.showOtpError = true;
          me.show_text_retry = false;
          me.otp_error = 'M OTP khng ng !';
        } else if(response.data === 3) {
          me.showOtpError = true;
          me.show_text_retry = false;
          me.otp_error = 'M OTP  ht hn !';
        } else if(response.data === 1) {
          me.showOtpError = false;
          me.show_text_retry = false;
          me.otp_error = '';
          me.show_otp= false;
          me.heading_text= "ng k AI Call Center";
          me.heading_sub_text= "Tri nghim tng i t ng";
          me.showSuccess=true;
          
        }
      })
    },
    onOtpReset(evt) {
      evt.preventDefault();
      /* Reset our form values */
      this.form.otp_code = "";
      /* Trick to reset/clear native browser form validation state */
      this.showOtpError = false
    },
    resendOTP(evt) {
      let me = this;
      axios.post('ajaxfile.php', {
        request: 4,
        tongdai: this.form.name_tongdai,
        congty: this.form.name_congty,
        name: this.form.name,
        email: this.form.email,
        phone: this.form.phone
      })
      .then(function (response) {
        me.show_text_retry= true;
      })
    }
  }
};
</script>

<style scoped lang="scss">
@import './register.scss';
</style>